@import 'https://fonts.googleapis.com/css?family=Share+Tech+Mono';

html {
  background: #a2d6fe;
  font-family: 'Share Tech Mono';
  color: #eee;
}

body {
  padding-top: 10%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.transform {
  -webkit-transform: scale(.9) rotateX(45deg) rotateZ(45deg) ;
          transform: scale(.9) rotateX(45deg) rotateZ(45deg) ;
  -webkit-transition: -webkit-transform .5s ease .5s;
  transition: -webkit-transform .5s ease .5s;
  -o-transition: transform .5s ease .5s;
  transition: transform .5s ease .5s;
  transition: transform .5s ease .5s, -webkit-transform .5s ease .5s;
}

.transform:hover {
  -webkit-transform: scale(1) rotateX(0) rotateZ(0);
          transform: scale(1) rotateX(0) rotateZ(0);
}

button {
  display: block;
  border: none;
  font-family: inherit;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  margin: 6px 4px;
  background: #eff0fe;
  color: #eb9ccf;
}

.calculator {
  margin: 100px auto;
  padding: 20px 25px 70px;
  width: 300px;
  background: #8e75bf;
  border-radius: 3px;
  -webkit-box-shadow: 4px 5px #5F4B8B, 5px 4px 0px #5F4B8B, 8px 8px 10px rgba(0, 0, 0, 0.2);
          box-shadow: 4px 5px #5F4B8B, 5px 4px 0px #5F4B8B, 8px 8px 10px rgba(0, 0, 0, 0.2);
}

.calculator-display {
  height: 55px;
  overflow: scroll;
  overflow: scroll;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  -webkit-box-shadow: inset 0px 4px rgba(0, 0, 0, 0.2);
          box-shadow: inset 0px 4px rgba(0, 0, 0, 0.2);
  line-height: 50px;
  font-size: 2em;
  padding: 0px 10px;
  margin: 8px;

  /*Inputs*/
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  letter-spacing: 1px;
  text-align: right;
}

.answer-row {
  width: -80%;
  margin-left: 70px;
}

.calculator-keypad {
  padding-top: 15px;
  height: 270px;
  width: 300px;
}

.button-row {
  display: -ms-flexbox;
  display: flex;
}

.calculator-key {
  top: 0;
  position: relative;
  width: 80px;
  height: 50px;
  cursor: pointer;
  border-radius: 3px;
  -webkit-box-shadow: 0px 4px rgba(95, 96, 101, 0.5), 0px 3px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 4px rgba(95, 96, 101, 0.5), 0px 3px 0px rgba(0, 0, 0, 0.2);
  font-size: 1.5em;
  text-shadow: 0px 1px rgba(0, 0, 0, 0.2);
}

.calculator-key:last-child {
  background: #fec3e8;
  color: #eee;
}

.button-row-1 > .calculator-key {
  background: #addfff;
  color: #eee;
}

/*Button press effect on hover and click*/
.calculator-key:hover {
  -webkit-box-shadow: 0px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 2px rgba(0, 0, 0, 0.2);
  top: 1px;
  cursor: pointer;
  }

.calculator-key:active {
  -webkit-box-shadow: 0px 0px;
          box-shadow: 0px 0px;
  top: 4px;
  }



